import {
  ADD_PRODUCT,
  REMOVE_PRODUCT,
  CHANGE_PRODUCT_QUANTITY,
} from './actionTypes';

const initialState = {
  products: [],
};

const cartReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_PRODUCT:
      return {
        ...state,
        products: [...state.products, action.payload],
      };
    case REMOVE_PRODUCT:
      return {
        ...state,
        productToRemove: action.payload,
      };
    case CHANGE_PRODUCT_QUANTITY:
      return {
        ...state,
        productToChange: action.payload,
      };
    default:
      return state;
  }
};

export {cartReducer};
